:root{
  --PRI-BG : #dddddd;
  --SHA-TOP : #eeeeee;
  --SHA-BOT : #aaaaaa;
  --TEXT-COL : #333333;
}

body{
  font-family: 'Nunito', sans-serif;
  margin: 10px;
  text-align: center;
  color: var(--TEXT-COL);
  background-color: var(--PRI-BG);
}

.grid-container{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;
}
a{
  text-decoration: none;
  color: var(--TEXT-COL);
}

input:focus{
  outline: none;
}

.button-container{
  width: 200px;
  height: auto;
  padding: 10px;
  border-radius: 10px;
  box-shadow:  10px 10px 20px var(--SHA-BOT), 
           -10px -10px 20px var(--SHA-TOP);
  margin: auto;
  margin-bottom: 20px;
  margin-top: 20px;

}

.cards-container{
  border-radius: 10px;
  width: 200px;
  height: 200px;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
  border: 2px solid var(--TEXT-COL);
  
}
.cards-container > p{
  margin: 30px;
}

.count{
  font-size: 40px;
}

@media only screen and (max-width: 800px){
  .grid-container{
      display: grid;
      grid-template-columns: auto;
  }  
  .cards-container{
      height: auto;
      margin-top: 20px;
      margin-bottom: 20px;
  }
}